.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 50s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

ul.temp-for-testing { list-style-type: none; margin: 0; padding: 0; overflow: hidden; }
ul.temp-for-testing li { float: left; padding: 4px 8px; }
ul.temp-for-testing li a, ul.temp-for-testing li button { font-weight: bold; color: blue; text-decoration: underline; }
